.next-trips {
  display: flex;
  align-content: flex-start;
  flex-wrap: wrap;
  width: 386px;
  padding: 0;
  border-right: 1px solid $gray;

  @media (max-width: $tablet-width) {
    width: 50%;
    margin-bottom: 18px;
  }

  @media (max-width: $mobile-width) {
    justify-content: center;
    margin-bottom: 29px;
    width: 100%;
    border-right: none;
    border-bottom: 1px solid $gray;
  }
}

.next-trips__list {
  margin: 0 50px 0 0;
  padding: 0;
  list-style: none;

  &:nth-of-type(3n) {
    margin-right: 0;

    @media (max-width: $mobile-width) {
      margin-top: 10px;
      margin-bottom: 19px;
    }
  }

  &:nth-of-type(2n) {
    @media (max-width: $mobile-width) {
      margin-right: 0;
    }
  }

  @media (max-width: $tablet-width) {
    margin: 0 48px 0 0;
  }

  @media (max-width: $mobile-width) {
    margin-right: 19px;
    width: 134px;
  }
}

.next-trips__link {
  display: block;
  margin-bottom: 10px;
  font-size: 14px;
  line-height: 17px;
  color: transparentize($gray--dark, 0.5);
}
