.offer {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding: 40px 30px 30px;
  width: 370px;
  min-height: 672px;
  box-shadow: 0 0 0 1px $gray;
  border-radius: 6px;
  transition-timing-function: ease-out;
  transition-duration: 0.3s;
  transition-property: box-shadow;
  background-color: $white;
  cursor: pointer;

  @media (max-width: $tablet-max-width) {
    width: 46%;
    padding: 28px 0 0;
    min-height: 627px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }

  @media (max-width: $mobile-width) {
    width: 288px;
    min-height: 625px;
    margin: 15px auto 18px;
    padding: 28px 0 0;
  }

  &:focus,
  &:hover {
    box-shadow: 0 0 0 3px $accent;
  }

  &:focus .offer__title,
  &:hover .offer__title {
    color: $accent;
  }

  &:focus .offer__text,
  &:hover .offer__text {
    border-color: $accent;
  }
}

.offer__title {
  display: flex;
  justify-content: space-between;
  margin: 0 0 25px;
  font-weight: 600;
  font-size: 28px;
  line-height: 34px;
  color: transparentize($gray--dark, 0.3);

  @media (max-width: $tablet-max-width) {
    flex-direction: column;
    align-items: center;
    margin-bottom: 13px;
    font-size: 24px;
  }

  &--econom {
    margin-bottom: 20px;
    line-height: 38px;

    @media (max-width: $tablet-max-width) {
      margin-bottom: 13px;
    }
  }
}

.offer__title-span {
  font-weight: 700;
  font-size: 25px;
  line-height: 34px;

  @media (max-width: $tablet-max-width) {
    margin-top: 10px;
  }

  &--econom {
    @media (max-width: $tablet-max-width) {
      margin-top: 6px;
    }
  }
}

.offer__text {
  padding-bottom: 28px;
  width: 280px;
  font-size: 16px;
  line-height: 24px;
  border-bottom: 1px solid transparentize($gray--dark, 0.7);
  color: $main-text;

  @media (max-width: $tablet-max-width) {
    margin: 0 15px 0;
    padding-bottom: 8px;
    width: auto;
    text-align: center;
  }
}

.offer__list {
  margin: 15px 0;
  padding: 0 10px 0 30px;
  list-style: none;

  @media (max-width: $tablet-max-width) {
    margin: 0 20px 10px;
    padding: 21px 0 0 30px;
  }
}

.offer__item {
  position: relative;
  font-size: 16px;
  line-height: 32px;
  color: $main-text;

  &::before {
    position: absolute;
    content: "";
    top: 6px;
    left: -31px;
    width: 22px;
    height: 22px;
    background-image: url("../img/icon-check-black.svg");
  }

  &--complex {
    padding: 0 0 10px;

    @media (max-width: $tablet-max-width) {
      padding: 0 0 8px;
    }
  }

  &--econom {
    padding: 0 0 15px;

    @media (max-width: $tablet-max-width) {
      padding: 0 0 10px;
    }

    @media (max-width: $mobile-width) {
      padding: 0 0 10px;
    }
  }

  &--econom::before {
    position: absolute;
    content: "";
    top: 10px;
    left: -31px;
    width: 22px;
    height: 22px;
    background-repeat: no-repeat;
    background-image: url("../img/icon-check-active.svg");
  }
}

.offer__button {
  padding: 28px 40px 32px;

  @media (max-width: $tablet-max-width) {
    margin: auto -1px -1px;
    padding: 20px 40px 18px;
    font-size: 10px;
    line-height: 12px;
  }

  &:hover,
  &:focus {
    background-color: $button-active;
  }
}
